import React from "react";
import UserService from "../services/UserService";

class UserComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      users: [],
    };
  }

  componentDidMount() {
    UserService.getUsers().then((response) => {
      this.setState({ users: response.data });
    });
  }

  render() {
    return (
      <div>
        <h1 className="text-center">User List</h1>
        <table className="table table-striped">
          <thead>
            <tr>
              <td>User id</td>
              <td>User token</td>
              <td>User firstName</td>
              <td>User secondName</td>
              <td>User sexe</td>
              <td>User country</td>
              <td>User city</td>
              <td>User address</td>
              <td>User birthday</td>
              <td>User birthplace</td>
              <td>User cin</td>
              <td>User phone</td>
              <td>User email</td>
              <td>User password</td>
              <td>User profil</td>
              <td>User groupe</td>
            </tr>
          </thead>
          <tbody>
            {this.state.users.map((user) => (
              <tr key={user.id_u}>
                <td>{user.id_u}</td>
                <td>{user.token}</td>
                <td>{user.firstName}</td>
                <td>{user.secondName}</td>
                <td>{user.sexe}</td>
                <td>{user.country}</td>
                <td>{user.city}</td>
                <td>{user.address}</td>
                <td>{user.birthday}</td>
                <td>{user.birthplace}</td>
                <td>{user.cin}</td>
                <td>{user.phone}</td>
                <td>{user.email}</td>
                <td>{user.password}</td>
                <td>{user.profil}</td>
                <td>{user.idG}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    );
  }
}

export default UserComponent;
